/**=====================
    24. According CSS start
==========================**/
.accordion {
  & > .card {
    box-shadow: none;
    border: 1px solid rgba(0, 0, 0, 0.15) !important;
    margin-bottom: 10px !important;

    .card-header {
      margin: -1px;
      padding: 10px 20px;
      background-color: #fff;
      border-color: 1px solid rgba(0, 0, 0, 0.15);

      .btn {
        padding: 0;
      }

      .btn-link {
        color: $primary-color;
        font-weight: 600;

        &:hover,
        &:focus {
          text-decoration: none;
        }
      }

      &.active {
        background-color: $primary-color;

        .btn-link {
          color: $white;
        }

      }
    }

    &:last-child {
      margin-bottom: 0 !important;
    }
  }
}

.default-according {
  .card {
    margin-bottom: 0;

    & + .card {
      margin-top: $according-card-top-margin;
    }

    .btn-link {
      font-weight: $according-btn-weight;
      color: $theme-body-font-color;
    }

    .btn-link {
      &:focus,
      &:hover {
        text-decoration: none;
      }
    }

    .card-header {
      padding: $according-card-header-padding;

      i {
        position: absolute;
        left: 18px;
        font-size: 20px;
        top: 20px;
      }

      h5 {
        margin-top: 2px;
      }
    }
  }
}

.default-according.style-1 {
  button {
    width: 100%;
    text-align: left;

    &:before {
      right: 20px;
      position: absolute;
      transition: 0.4s;
    }

    &[aria-expanded="true"] {
      &:before {
        content: $according-open-icon;
        font-family: $according-card-header-icon;
      }
    }

    &[aria-expanded="false"] {
      &:before {
        content: $according-close-icon;
        font-family: $according-card-header-icon;
      }
    }
  }
}

/**=====================
     24. According CSS Ends
==========================**/
