/**=====================
     01. General CSS Start
==========================**/
//Custom css Start
body {
  font-size: $body-font-size;
  overflow-x: hidden;
  font-family: $font-cairo, $font-serif;
  color: $logo-color-3;
  background-color: $theme-body-bg-color;
}

ul {
  padding-left: $ul-padding-left;
  list-style-type: none;
  margin-bottom: 0;
  padding-right: $ul-padding-right;
}

* a {
  color: $primary-color;
  text-decoration: none;
}

*.btn:focus {
  box-shadow: $btn-focus-box-shadow !important;
}

p {
  font-size: $paragraph-font-size;
  line-height: $paragraph-line-height;
  letter-spacing: $paragraph-letter-spacing;
}


.disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

svg {
  vertical-align: baseline;
}

input {
  &:focus {
    outline-color: $transparent-color;
  }
}

label {
  margin-bottom: 0.5rem;
}

.media-widgets {
  .media-body {
    margin-bottom: 30px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
}

//Custom css End

//Typography css start

.typography {

  h1,
  .h1,
  h2,
  .h2,
  h3,
  .h3,
  h4,
  .h4,
  h5,
  .h5,
  h6,
  .h6 {
    margin: 20px 0;
    margin-top: 0;

    &:last-child {
      margin-bottom: 0;
    }
  }

  //   small {
  //     padding-left: $small-tag-padding;
  //     color: $small-tag-color;
  //   }
}

// pre {
//   background-color: $pre-tag-bg-color;
//   padding: $pre-tag-padding;
// }

a {
  &:hover {
    text-decoration: none;
    color: $primary-color;
  }
}

//Typography css end

.click2edit {
  & ~ .note-editor {
    &.note-frame {
      margin-bottom: 30px;
    }
  }
}

.note-editor {
  &.note-frame {
    border-color: $light-color;
  }
}

.note-editor {
  &.note-frame {
    border-color: $light-color;
  }
}

// modal page
.modal {
  .modal-body {
    .card {
      box-shadow: none !important;
    }
  }
}

.modal-dialog {
  .modal-content {
    animation: modal-animation 0.5s ease;

    .modal-body {
      p {
        a {
          margin-right: 0;
        }
      }
    }
  }
}

@keyframes modal-animation {
  0% {
    opacity: 0;
    transform: translate3d(0, -50px, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

// index page notify
.alert-theme {
  button {
    top: 33px !important;
    right: 30px !important;
    color: $primary-color;
  }

  span {
    + span {
      + span {
        // border: 2px solid $primary-color;
        background-color: lighten($primary-color, 40%);
        color: $primary-color;
        border-radius: 4px;
        box-shadow: 0 2px 14px -3px rgba($black, 0.2);
      }
    }
  }

  i {
    padding: 20px;
    padding-right: 0 !important;
    margin-right: 20px !important;
  }

  .progress {
    margin-top: -2px !important;
    height: 2px;

    .progress-bar {
      background-color: $primary-color;
    }
  }
}

.alert-copy {
  border: 1px solid $primary-color;
  line-height: 1;
  display: inline-block;
  width: 300px;
  color: $white;
  background-color: $primary-color;

  .close {
    box-shadow: none;
    color: $white;
    line-height: 1.4;
    font-weight: 100;
  }
}

.ace-editor {
  height: 400px;
  width: 100%;
}

.editor-toolbar {
  width: 100% !important;
}

.CodeMirror {
  top: 0 !important;
}

// rangeslider
.irs-from,
.irs-to,
.irs-single {
  background: $primary-color;

  &:after {
    border-top-color: $primary-color;
  }
}

.irs-line-mid,
.irs-line-left,
.irs-line-right,
.irs-bar,
.irs-bar-edge {
  background-color: $primary-color;
}

// sweetalert css
.swal-button {
  background-color: $primary-color;

  &:active {
    background-color: darken($primary-color, 5%);
  }
}

.swal-button--cancel {
  background-color: #efefee !important;
}

.clipboaard-container {
  h6 {
    line-height: 1.6;
  }
}

fieldset {

  .bootstrap-touchspin-postfix,
  .bootstrap-touchspin-prefix {
    border-radius: 0;
    padding: 0.4rem 0.75rem;
  }

  .input-group-prepend {
    & ~ .form-control {
      height: 43px;
    }
  }

  .input-group-lg {

    .input-group-prepend,
    .input-group-append {
      .btn {
        padding: 0.8rem 1.75rem;
      }
    }

    .input-group-prepend {
      & ~ .form-control {
        height: 58px;
      }
    }
  }
}

.f1 .f1-steps .f1-step .f1-step-icon {
  padding: 12px;
}

.form-inline .form-group {
  display: flex;
}

.input-group-text {
  i {
    line-height: 1.5;
  }
}

/**=====================
      01. General CSS Ends
==========================**/
