.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

/**=====================
     01. General CSS Start
==========================**/
body {
  font-size: 14px;
  overflow-x: hidden;
  font-family: "Cairo", sans-serif;
  color: #051418;
  background-color: #f0f2f7;
}

ul {
  padding-left: 0px;
  list-style-type: none;
  margin-bottom: 0;
  padding-right: 0px;
}

* a {
  color: #6294C2;
  text-decoration: none;
}

*.btn:focus {
  box-shadow: none !important;
}

p {
  font-size: 13px;
  line-height: 1.7;
  letter-spacing: 0.7px;
}

.disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

svg {
  vertical-align: baseline;
}

input:focus {
  outline-color: transparent;
}

label {
  margin-bottom: 0.5rem;
}

.media-widgets .media-body {
  margin-bottom: 30px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
}

.typography h1,
.typography .h1,
.typography h2,
.typography .h2,
.typography h3,
.typography .h3,
.typography h4,
.typography .h4,
.typography h5,
.typography .h5,
.typography h6,
.typography .h6 {
  margin: 20px 0;
  margin-top: 0;
}
.typography h1:last-child,
.typography .h1:last-child,
.typography h2:last-child,
.typography .h2:last-child,
.typography h3:last-child,
.typography .h3:last-child,
.typography h4:last-child,
.typography .h4:last-child,
.typography h5:last-child,
.typography .h5:last-child,
.typography h6:last-child,
.typography .h6:last-child {
  margin-bottom: 0;
}

a:hover {
  text-decoration: none;
  color: #6294C2;
}

.click2edit ~ .note-editor.note-frame {
  margin-bottom: 30px;
}

.note-editor.note-frame {
  border-color: #f4f4f4;
}

.note-editor.note-frame {
  border-color: #f4f4f4;
}

.modal .modal-body .card {
  box-shadow: none !important;
}

.modal-dialog .modal-content {
  animation: modal-animation 0.5s ease;
}
.modal-dialog .modal-content .modal-body p a {
  margin-right: 0;
}

@keyframes modal-animation {
  0% {
    opacity: 0;
    transform: translate3d(0, -50px, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}
.alert-theme button {
  top: 33px !important;
  right: 30px !important;
  color: #6294C2;
}
.alert-theme span + span + span {
  background-color: #f5f8fb;
  color: #6294C2;
  border-radius: 4px;
  box-shadow: 0 2px 14px -3px rgba(0, 0, 0, 0.2);
}
.alert-theme i {
  padding: 20px;
  padding-right: 0 !important;
  margin-right: 20px !important;
}
.alert-theme .progress {
  margin-top: -2px !important;
  height: 2px;
}
.alert-theme .progress .progress-bar {
  background-color: #6294C2;
}

.alert-copy {
  border: 1px solid #6294C2;
  line-height: 1;
  display: inline-block;
  width: 300px;
  color: #fff;
  background-color: #6294C2;
}
.alert-copy .close {
  box-shadow: none;
  color: #fff;
  line-height: 1.4;
  font-weight: 100;
}

.ace-editor {
  height: 400px;
  width: 100%;
}

.editor-toolbar {
  width: 100% !important;
}

.CodeMirror {
  top: 0 !important;
}

.irs-from,
.irs-to,
.irs-single {
  background: #6294C2;
}
.irs-from:after,
.irs-to:after,
.irs-single:after {
  border-top-color: #6294C2;
}

.irs-line-mid,
.irs-line-left,
.irs-line-right,
.irs-bar,
.irs-bar-edge {
  background-color: #6294C2;
}

.swal-button {
  background-color: #6294C2;
}
.swal-button:active {
  background-color: #5087bb;
}

.swal-button--cancel {
  background-color: #efefee !important;
}

.clipboaard-container h6 {
  line-height: 1.6;
}

fieldset .bootstrap-touchspin-postfix,
fieldset .bootstrap-touchspin-prefix {
  border-radius: 0;
  padding: 0.4rem 0.75rem;
}
fieldset .input-group-prepend ~ .form-control {
  height: 43px;
}
fieldset .input-group-lg .input-group-prepend .btn,
fieldset .input-group-lg .input-group-append .btn {
  padding: 0.8rem 1.75rem;
}
fieldset .input-group-lg .input-group-prepend ~ .form-control {
  height: 58px;
}

.f1 .f1-steps .f1-step .f1-step-icon {
  padding: 12px;
}

.form-inline .form-group {
  display: flex;
}

.input-group-text i {
  line-height: 1.5;
}

