.loader-wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  background-color: #fff;
  z-index: 11;
  top: 0;
  /*Color Loader*/
  /*Color Loader*/
}
@keyframes colors {
  0% {
    fill: #FBD301;
  }
  25% {
    fill: #FF3270;
  }
  50% {
    fill: #208BF1;
  }
  75% {
    fill: #AFE102;
  }
  100% {
    fill: #FBD301;
  }
}
@keyframes rotate-1 {
  20% {
    transform: rotate(180deg) translateX(-100px);
  }
  25% {
    transform: rotate(180deg) translateX(0);
  }
}
@keyframes rotate-2 {
  25% {
    transform: none;
  }
  45% {
    transform: rotate(180deg) translateY(100px);
  }
  50% {
    transform: rotate(180deg) translateY(0);
  }
}
@keyframes rotate-3 {
  50% {
    transform: none;
  }
  70% {
    transform: rotate(180deg) translateX(100px);
  }
  75% {
    transform: rotate(180deg) translateX(0);
  }
}
@keyframes rotate-4 {
  75% {
    transform: none;
  }
  95% {
    transform: rotate(180deg) translateY(-100px);
  }
  100% {
    transform: rotate(180deg) translateY(0);
  }
}
.loader-wrapper circle {
  transform-origin: center;
  animation: colors ease 4s infinite;
}
.loader-wrapper circle:nth-child(2) {
  animation: colors ease 4s infinite, rotate-1 ease 4s infinite;
}
.loader-wrapper circle:nth-child(3) {
  animation: colors ease 4s infinite, rotate-2 ease 4s infinite;
}
.loader-wrapper circle:nth-child(4) {
  animation: colors ease 4s infinite, rotate-3 ease 4s infinite;
}
.loader-wrapper circle:nth-child(5) {
  animation: colors ease 4s infinite, rotate-4 ease 4s infinite;
}
.loader-wrapper body {
  font-family: sans;
  min-height: 100vh;
  margin: 0;
  display: grid;
  place-content: center;
  background: black;
}
.loader-wrapper h1 {
  color: #fff;
  text-align: center;
  margin-bottom: 2em;
  letter-spacing: 3px;
  position: relative;
  z-index: 2;
}
.loader-wrapper h1:before {
  position: absolute;
  content: "";
  height: 1px;
  background: linear-gradient(to right, #f73164, #6294C2, #a927f9);
  color: #fff;
  top: 2.75em;
  left: 0.75em;
  width: 2.5em;
  font-weight: 400;
  font-variant: small-caps;
  font-size: 0.5em;
}

.loader-box {
  height: 150px;
  text-align: center;
  display: flex;
  align-items: center;
  vertical-align: middle;
  justify-content: center;
  transition: 0.3s color, 0.3s border, 0.3s transform, 0.3s opacity;
}
.loader-box [class*=loader-] {
  display: inline-block;
  width: 50px;
  height: 50px;
  color: inherit;
  vertical-align: middle;
}
.loader-box .loader-1 {
  border: 0.2em dotted #6294C2;
  border-radius: 50%;
  animation: 1s loader-01 linear infinite;
}
@keyframes loader-01 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader-box .loader-2 {
  border: 0.2em solid transparent;
  border-left-color: #f73164;
  border-right-color: #f73164;
  border-radius: 50%;
  animation: 1s loader-02 linear infinite;
}
@keyframes loader-02 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader-box .loader-3 {
  border: 0.2em solid #51bb25;
  border-bottom-color: transparent;
  border-radius: 50%;
  animation: 1s loader-03 linear infinite;
  position: relative;
}
@keyframes loader-03 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader-box .loader-4 {
  border: 1px solid #a927f9;
  border-radius: 50%;
  animation: 1s loader-04 linear infinite;
  position: relative;
}
.loader-box .loader-4:before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  top: -0.2em;
  left: 50%;
  border: 0.2em solid #a927f9;
  border-radius: 50%;
}
@keyframes loader-04 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader-box .loader-5 {
  border: 0.2em solid transparent;
  border-top-color: #f8d62b;
  border-radius: 50%;
  animation: 1s loader-05 linear infinite;
  position: relative;
}
.loader-box .loader-5:before {
  content: "";
  display: block;
  width: inherit;
